.Filters{display:flex;flex-direction:column;align-items:flex-start;justify-content:flex-start;width:270px;background:#fff}.Filters span{font-size:16px;font-weight:500}.Filters__tittle{width:100%;border:1px solid #e6e6e6;padding:20px 20px}.Filters__tittle span{font-size:18px;margin-left:10px}.Filters__price{padding:20px 20px;border:1px solid #e6e6e6}.Filters__price .price-inputs{display:flex;justify-content:space-between;width:100%;margin-top:20px;position:relative}.Filters__price .price-inputs::after,.Filters__price .price-inputs::before{position:absolute;top:50%;transform:translateY(-50%);color:#b3b3b3}.Filters__price .price-inputs::before{content:"от";left:15px}.Filters__price .price-inputs::after{content:"до";right:78px}.Filters__price .price-inputs input{border:1px solid #e6e6e6;border-radius:2px;height:35px;width:110px;text-align:center}.Filters__price .price-inputs input:first-child{margin-right:13px}.Filters__price .price-slider{margin-top:20px}.Filters__types{display:flex;flex-direction:column;padding:20px 20px;width:100%;border:1px solid #e6e6e6}.Filters__types span{margin-bottom:20px}.Filters__types label{cursor:pointer;user-select:none}.Filters__types label input{margin:10px}